import React, {Component} from 'react';
import ReactHowler from 'react-howler';
import {volumeSliderStyle} from '../js/input';

class Sound extends Component{
  constructor(props){
    super(props);

    this.state = {
      name: this.props.sound.name,
      fade: 1,
      editingName: false
    }
    this.nameInputKeypress = this.nameInputKeypress.bind(this);
    this.nameInputChange = this.nameInputChange.bind(this);
    this.renderProgress = this.renderProgress.bind(this);
  }
  componentDidMount(){
    this.timer = setInterval(this.renderProgress,100);
  }
  componentWillUnmount(){
    clearInterval(this.timer);
  }

  renderProgress(){
    if(this.props.sound.playing){
      this.forceUpdate();
    }
  }

  nameInputKeypress(e){
    const code = (e.keyCode ? e.keyCode : e.which);
    if(code == 13){
      if(e.target.value.trim() !== ""){
        this.props.changeVar(this.props.sound.id, 'name', e.target.value.trim());
      }
      this.setState({editingName: false});
    }
  }
  nameInputChange(e){
    this.setState({name:e.target.value});
  }

  render(){
    const {
      sound,
      style,
      removeSound,
      adjustVolume,
      playSound,
      pauseSound,
      stopSound,
      onSoundLoad,
      onFinishPlaying,
      replaceSound
    } = this.props;

    const name = () =>{
      if(this.state.editingName){
        return <input type="text" onKeyPress={this.nameInputKeypress} ref={(input) => {this.editNameInput = input}} onChange={this.nameInputChange} onBlur={()=> this.setState({editingName:false})} value={this.state.name}></input>;
      }
      return <h3 onClick={()=>this.setState({editingName:true})}>{sound.name}</h3>;
    }
    const controls = () =>{
      if(sound.playing){
        return(
          <span>
            <button type="button" name="button" className="pause-button" onClick={()=>pauseSound(sound.id)}><i className="fas fa-pause"></i></button>
            <button type="button" name="button" className="stop-button"><i className="fas fa-square" onClick={()=>stopSound(sound.id, this.player)}></i></button>
          </span>
        )
      }
      else{
        return (
          <span>
            <button type="button" name="button" className="play-button" onClick={()=>playSound(sound.id)}><i className="fas fa-play"></i></button>
            <button type="button" name="button" className="stop-button"><i className="fas fa-square" onClick={()=>stopSound(sound.id, this.player)}></i></button>
          </span>
        )
      }
    }
    const progress = () =>{
      if( typeof this.player  !== 'undefined' && this.player.howlerState() === 'loaded'){
        const x = (this.player.seek() / this.player.duration()) * 100;
        return {width: x+"%"}
      }
      return {};
    }
    const volStyles = () =>{
      return volumeSliderStyle(sound.volume);
    }
    const vol = (this.props.effectVol/100) * (this.props.sound.volume/100) * this.state.fade;

    return(
      <div className="sound-file">
        <div className="sound-file_header">
          <button type="button" name="replace" className="replace-file-button" onClick={()=>replaceSound(sound.id)}><i className="fas fa-upload"></i></button>
          {name()}
          <button type="button" name="delete" className="delete-button" onClick={()=>removeSound(sound)}> <i className="fas fa-times"></i> </button>
        </div>
        <div className="sound-file_body">
          <div className="sound-file_progress" style={progress()}></div>
          <div className="sound-file_content">
            <div className="sound-file_controls">
              {controls()}
            </div>
            <div className="sound-file_volume">
              <i className="fas fa-volume-off"></i> <input type="range" min="0" max="100" name="" style={volStyles()} value={sound.volume} className="volume-slider" onChange={(e)=>adjustVolume(sound.id, e.target.value)}/>
            </div>
          </div>
        </div>
        <ReactHowler
          src={sound.url}
          format={[sound.ext]}
          playing={sound.playing}
          volume={vol}
          loop={true && this.props.style !== 'random' ? true : false}
          preload={true}
          onLoad={()=>onSoundLoad(sound.id)}
          onEnd={()=>onFinishPlaying(sound.id)}
          ref={(ref) => (this.player = ref)}/>
      </div>
    );
  }
}

export default Sound;



