import * as types from './effectsConsts';
import {effectBuilder} from './effectsConsts';

export function loadEffects(effects){
  return{
    type: types.LOAD_EFFECTS,
    effects
  }
}

export function addEffect(){
  const effect = effectBuilder("Effect");
  return{
    type: types.ADD_EFFECT,
    payload:effect
  }
}

export function removeEffect(effect){
  return{
    type: types.REMOVE_EFFECT,
    id: effect.id
  }
}

export function changeEffectVolume(id, volume){
  return{
    type: types.CHANGE_EFFECT_VOLUME,
    effect: {
      id,
      volume
    }
  }
}

export function addSoundToEffect(sound, effectId){
  return{
    type: types.ADD_SOUND_TO_EFFECT,
    payload:{
      effectId,
      soundId: sound
    }
  }
}

export function removeSoundReference(sound){
  return{
    type: types.REMOVE_SOUND_REFERENCE,
    sound
  }
}

export function changeEffectVar(id, varName, value, extras = null){
  return{
    type: types.CHANGE_EFFECT_VAR,
    payload:{
      id,
      varName,
      value,
      extras
    }
  }
}



