import * as types from '../actions/effectsConsts';

export default function reducer(state={}, action){
  switch(action.type){
    case types.LOAD_EFFECTS:{
      return action.effects;
    }
    case types.ADD_EFFECT:{
      return {...state, [action.payload.id]:action.payload}
    }
    case types.REMOVE_EFFECT:{
      const effects = Object.assign({}, state);
      delete effects[action.id];
      return effects;
    }
    case types.CHANGE_EFFECT_VOLUME:{
      return updateState(action.payload.effect.id, 'volume', action.payload.effect.volume);
    }
    case types.ADD_SOUND_TO_EFFECT:{
      const sounds = state[action.payload.effectId].sounds.concat(action.payload.soundId);
      return updateState(action.payload.effectId, 'sounds', sounds);
    }
    case types.REMOVE_SOUND_REFERENCE:{
      const sounds = state[action.sound.effectId].sounds.filter(s => s !== action.sound.id);
      return updateState(action.sound.effectId, 'sounds', sounds);
    }

    case types.CHANGE_EFFECT_VAR:{
      const {id, varName, value} = action.payload;
      return updateState(id, varName, value);
    }
    default: {return state}
  }

  function updateState(id, varName, value){
    return{
      ...state,
      [id]: Object.assign({}, state[id],{[varName]:value})
    }
  }
}



