import * as types from './soundsConsts';

export function loadSounds(sounds){
  return{
    type: types.LOAD_SOUNDS,
    sounds
  }
}

export function createSound(sound){
  return{
    type: types.CREATE_SOUND,
    payload: sound
  }
}

export function replaceSound(id, file, fileUrl){
  return{
    type: types.REPLACE_SOUND,
    payload: {
      id,
      file,
      fileUrl
    }
  }
}

export function removeSound(sound){
  return{
    type: types.REMOVE_SOUND,
    id: sound.id
  }
}

export function changeSoundVar(id, varName, value, extras = null){
  return{
    type: types.CHANGE_SOUND_VAR,
    payload:{
      id,
      varName,
      value,
      extras
    }
  }
}



