export function volumeSliderStyle(vol, light = "#f6277a", dark = "#4a031f"){
  const min = vol;
  const max = parseInt(min) + 1;
  const grad = `linear-gradient(to right, ${light}, ${light} ${min}%, ${dark} ${max}%, ${dark})`
  return {
    backgroundSize: '100% 5px',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat',
    background: grad
  };
}



