import React, {Component} from 'react';
import {connect} from 'react-redux';
import JSZip from 'jszip';
import FileSaver from 'file-saver';

import Effect from '../components/effect';
import {addEffect, removeEffect, addSoundToEffect, changeEffectVar, loadEffects} from '../actions/effectsActions';

import {createSound, loadSounds} from '../actions/soundsActions';
import {soundBuilder} from '../actions/soundsConsts';

import {objKeysToArr, objKeyArray, getFileExtension, formatAudioObj} from '../Helpers'

import '../style/effectcard.css';

class EffectContainer extends Component{
  constructor(props){
    super(props);

    this.adjustVolume = this.adjustVolume.bind(this);
    this.exportData = this.exportData.bind(this);
    this.loadData = this.loadData.bind(this);
  }

  exportData(){
    const {sounds} = this.props;
    const str = JSON.stringify(this.props.state);
    const file = new File([str], 'meta.json', {type: "text/plain;charset=utf-8"});
    const soundArr = objKeysToArr(sounds)

    let zip = new JSZip();

    zip.file("meta.json", file);

    soundArr.forEach((sound)=>{
      zip.folder("sounds")
      .folder(sound.id)
      .file(sound.id + '.' + getFileExtension(sound.file.name).toLowerCase(), sound.file)
    });

    zip.generateAsync({type: "blob"}).then((content)=>{
      FileSaver.saveAs(content,'Audio.scrump');
    });
  }
  loadData(e){
    const file = e.target.files[0];
    let effects, sounds = {};
    let meta = null;

    //finish = finish.bind(this);

    getMeta();

    function getMeta(){
      console.log("GET META");
      JSZip.loadAsync(file).then((zip)=>{
        try{
          zip.file('meta.json').async('string').then(function success(content){
            meta = JSON.parse(content);
            effects = meta.effects;
            sounds = meta.sounds;
            console.log("META", meta);
            getAudio(zip);
          });
        } catch(e){
          alert("No meta.json file was found in the archive");
        }
      });
    }

    function getAudio(zip){
      console.log("GET AUDIO");
      let audio = {};
      let fileTotal = 0;
      let fileCount = 1;

      zip.forEach((path,entry)=>{
        if(!entry.dir){fileTotal++;}
      });

      console.log("FILE TOTAL: ", fileTotal);
      zip.forEach((path,entry)=>{
        if(!entry.dir){
          console.log("FILE COUNT: ", fileCount);
          if(entry.name !== 'meta.json'){
            zip.file(path).async('blob').then(function success(content){
              fileCount++;
              Object.assign(audio,formatAudioObj(content, entry.name, audio));
              if(fileCount === fileTotal){console.log("FINISH");finish(audio);}
            }, function error(e){
              alert(e);
            });
          }
        }
      });
    }

    const finish = (function finish(audio){
      const audioArr = objKeyArray(audio);
      const blob = window.URL || window.webkitURL;

      audioArr.forEach((s) =>{
        sounds[s].url = blob.createObjectURL(audio[s]);
        sounds[s].file = audio[s];
        sounds[s].playing = false;
      });
      console.log(sounds);
      this.props.load(effects, sounds);
    }).bind(this);
  }

  adjustVolume(effectId, value){
    this.props.changeVar(effectId, 'volume', value);
  }

  render(){
    const effects = objKeysToArr(this.props.effects).map((effect, i) => {
      return (
        <div className="col-lg-4 col-md-5" key={effect.id}>
          <Effect
          key={effect.id}
          effect={effect}
          deleteEffect={this.props.deleteEffect}
          changeVar={this.props.changeVar}
          createSounds={this.props.createSounds}
          adjustVolume={this.adjustVolume}
          />
        </div>
      );
    });
    return(
      <div>
        <button onClick={this.props.addEffect}>Add Effect</button>
        <button onClick={this.exportData}>Export</button>
        <input type="file" onChange={this.loadData}></input>
        <div className="row">
          {effects}
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    effects: state.effects,
    sounds: state.sounds,
    state: state
  }
}
const mapDispatchToProps = (dispatch) =>{
  return{
    addEffect: () => {
      dispatch(addEffect());
    },
    deleteEffect: (effect) =>{
      dispatch(removeEffect(effect));
    },
    createSounds:(effectId, fileArr)=>{
      dispatch(function(dispatch){
        fileArr.forEach((file)=>{
          const sound = soundBuilder(effectId, file.file, file.fileUrl);
          dispatch(createSound(sound));
          dispatch(addSoundToEffect(sound.id, effectId));
        });
      });
    },
    changeVar: (id, varName, value, extras)=>{
      dispatch(changeEffectVar(id,varName,value));
    },
    load: (effects, sounds) =>{
      dispatch((dispatch)=>{
        dispatch(loadSounds(sounds));
        dispatch(loadEffects(effects));
      });
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(EffectContainer);



