import React, {Component} from 'react';
import {connect} from 'react-redux';

import {removeSoundReference} from '../actions/effectsActions';

import Sound from '../components/sound';
import {replaceSound, removeSound, changeSoundVar} from '../actions/soundsActions';

import {objKeysToArr, simulateClick, getFileExtension} from '../Helpers';
import {volumeSliderStyle} from '../js/input';

const blob = window.URL || window.webkitURL;

class SoundsContainer extends Component{
  constructor(props){
    super(props);

    this.stopOtherSounds = this.stopOtherSounds.bind(this);
    this.adjustVolume = this.adjustVolume.bind(this);

    this.playSound = this.playSound.bind(this);
    this.playRandomSound = this.playRandomSound.bind(this);
    this.pauseSound = this.pauseSound.bind(this);
    this.stopSound = this.stopSound.bind(this);
    this.replaceSound = this.replaceSound.bind(this);
    this.replaceSoundChangeListener = this.replaceSoundChangeListener.bind(this);
    this.newSound = this.newSound.bind(this);

    this.onSoundLoad = this.onSoundLoad.bind(this);
    this.onFinishPlaying = this.onFinishPlaying.bind(this);
  }

  stopOtherSounds(id){
    const sounds = this.props.effect.sounds.filter((s) => s !== id);
    this.props.stopSounds(sounds.filter((s) => this.props.sounds[s].playing));
  }
  adjustVolume(soundId,value){
    this.props.changeVar(soundId, 'volume', value);
  }

  newSound(e) {
    if(e.target.files.length === 0) {return;}
    const files = e.target.files
    const fileArr = [];

    Array.from(files).forEach((file)=>{
      if(typeof this.processFile(e.target.files[0]) == 'undefined'){return;}
      fileArr.push({file, fileUrl: blob.createObjectURL(file)});
    });

    console.log(fileArr);

    if(fileArr.length === 0){return;}

    const {effect} = this.props;

    this.props.createSounds(effect.id, fileArr);
  }

  processFile(file){
    if (typeof file === 'undefined') {return;}

    const type = getFileExtension(file.name).toLowerCase();

    if (!(['wav', 'mp3'].indexOf(type) > -1)) {
      return;
    }

    return file;
  }

  playSound(id){
    if(id){
      this.props.changeVar(id, 'playing', true);
      this.stopOtherSounds(id);
    }
  }
  playRandomSound(){
    const id = this.getRandomSound(objKeysToArr(this.props.sounds));
    this.stopOtherSounds(id);
    this.playSound(id);
  }
  pauseSound(id){
    this.props.changeVar(id, 'playing', false)
  }
  stopSound(id, player){
    this.pauseSound(id);
    player.stop();
  }
  replaceSound(soundId){
    this.uploadReplace.removeEventListener("change", this.replaceSoundChangeListener);
    this.uploadReplace.addEventListener("change",this.replaceSoundChangeListener);
    this.uploadReplace.soundId = soundId;
    simulateClick(this.uploadReplace);
  }

  replaceSoundChangeListener(e){
    const file = this.processFile(e.target.files[0]);

    if(typeof file == 'undefined'){return;}
    const fileURL = blob.createObjectURL(file);

    this.props.replaceSound(e.target.soundId, file, fileURL);
  }

  onSoundLoad(id){
    this.props.changeVar(id, 'loaded', true);
  }
  onFinishPlaying(id){
    const s = 'random';
    switch(s){
      case 'random':{
        this.playSound(this.getRandomSound(objKeysToArr(this.props.sounds),id));
      }
    }
  }

  getRandomSound(sounds, id = '-1'){
    console.log(sounds.length);
    if(sounds.length === 0 || typeof sounds.length === 'undefined'){return;}

    if(sounds.length > 1){
      let newSound = {id: id};
      while(newSound.id === id){
        newSound = sounds[Math.floor(Math.random() * sounds.length)];
      }
      return newSound.id;
    }
    return this.pauseSound(sounds[0].id);
  }

  render(){
    const sounds = [];
    const {removeSound, changeVar, effect, adjustVolume} = this.props;
    const volStyles = () =>{
     return volumeSliderStyle(this.props.effect.volume)
    }

    effect.sounds.forEach((s)=>{
      const sound = this.props.sounds[s];
      console.log(sound);
      if(typeof sound === 'undefined'){console.log("UNDEFINED");return;}
      sounds.push(
        <Sound
          key={sound.id}
          sound={sound}
          effectVol={effect.volume}
          removeSound={removeSound}
          changeVar={changeVar}
          style={'random'}
          replaceSound={this.replaceSound}
          playSound={this.playSound}
          pauseSound={this.pauseSound}
          stopSound={this.stopSound}
          onSoundLoad={this.onSoundLoad}
          onFinishPlaying={this.onFinishPlaying}
          adjustVolume={this.adjustVolume}/>)
    });

    return(
      <div className="effect-card_content">
        <div className="effect-card_cat">
          <h2>SOUNDS</h2><i className="fas fa-volume-off"></i><input type="range" min="0" max="100" name="" style={volStyles()} value={effect.volume} onChange={(e)=>adjustVolume(effect.id, e.target.value)}/>
          <hr/>
        </div>
        {sounds}
        <div className="sound-file new">
          <div className="sound-file_body">
            <div className="sound-file_content">
              <input type="file" style={{display: "none"}} onChange={this.newSound} ref={(ref)=>(this.uploadNew = ref)} multiple/>
              <div onClick={(e) => simulateClick(this.uploadNew)} className="new-sound-wrapper">
                <button className='new-sound-button'>+</button>
                <h3>Add Another Sound</h3>
              </div>
            </div>
          </div>
        </div>
        <input type="file" style={{display: "none"}} ref={(ref) =>(this.uploadReplace = ref)}/>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) =>{
  let theseSounds = {};
  const effect = state.effects[ownProps.effectId];

  if(effect.sounds){
    effect.sounds.forEach((s)=>{
      Object.assign(theseSounds,{[s]:state.sounds[s]})
    });
  }

  return{
    effect: effect,
    sounds: theseSounds
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    replaceSound: (id, file, fileUrl) =>{
      dispatch(replaceSound(id, file,fileUrl))
    },
    removeSound: (sound) =>{
      dispatch(function(dispatch){
        dispatch(removeSoundReference(sound));
        dispatch(removeSound(sound));
      });
    },
    changeVar: (id, varName, value, extras)=>{
      dispatch(changeSoundVar(id,varName,value,extras));
    },
    stopSounds: (ids)=>{
      dispatch((dispatch)=>{
        ids.forEach((id)=>{
          console.log('CALLING on ', id);
          dispatch(changeSoundVar(id,'playing',false));
        });
      });
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(SoundsContainer);



