import React, {Component} from 'react';

import SoundsContainer from '../containers/SoundsContainer';
import {simulateClick} from '../Helpers';


class Effect extends Component {
  constructor(props){
    super(props);

    this.state = {
      name: this.props.effect.name,
      editingName: false
    }

    this.nameInputKeypress = this.nameInputKeypress.bind(this);
    this.nameInputChange = this.nameInputChange.bind(this);
  }

  componentDidUpdate(prevProps, prevState, snapshot){
    if(prevState.editingName === false && this.state.editingName === true){
      this.editNameInput.focus();
    }
  }

  nameInputKeypress(e){
    const code = (e.keyCode ? e.keyCode : e.which);
    if(code == 13){
      if(e.target.value.trim() !== ""){
        this.props.changeVar(this.props.effect.id, 'name', e.target.value.trim());
      }
      this.setState({editingName: false});
    }
  }
  nameInputChange(e){
    this.setState({name:e.target.value});
  }

  render() {
    const {effect, deleteEffect, adjustVolume, createSounds} = this.props;
    const name = () =>{
      if(this.state.editingName){
        return <input type="text" onKeyPress={this.nameInputKeypress} ref={(input) => {this.editNameInput = input}} onChange={this.nameInputChange} onBlur={()=> this.setState({editingName:false})} value={this.state.name}></input>;
      }
      return <h2 onClick={()=>this.setState({editingName:true})}>{effect.name}</h2>;
    }
    return (
      <div className="effect-card">
        <div className="effect-card_header">
          {name()}
          <button className="delete-button" onClick={() => deleteEffect(effect)}><i className="fas fa-times"></i></button>
        </div>
        <hr></hr>
        <div className="effect-card_body">
          <SoundsContainer effectId={effect.id} adjustVolume={adjustVolume} createSounds={createSounds}/>
        </div>
      </div>);
  }
}

export default Effect;



