import UUID from 'uuid/v4'
import {formatFileName, getFileExtension} from '../Helpers'

export const LOAD_SOUNDS = "LOAD_SOUNDS";
export const CREATE_SOUND = "CREATE_SOUND";
export const REPLACE_SOUND = "REPLACE_SOUND";
export const REMOVE_SOUND = "REMOVE_SOUND";
export const CHANGE_SOUND_VAR = "CHANGE_SOUND_VAR";

export function soundBuilder(effectId, file, fileURL){
  return {
    id: UUID(),
    effectId: effectId,
    file: file,
    fileName: file.name,
    name: formatFileName(file.name),
    ext: getFileExtension(file.name),
    url: fileURL,
    playing: false,
    loaded: false,
    volume: 100,
  }
}



